@use 'base/variables' as v;
@use 'base/mixins' as m;

.categorias {
    margin-top:  3rem;

    h1{
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 0;
    }

    &__grid{
       @include m.grid(3, 2rem); 
    }

    &__card {
        padding: 1rem;
        @include m.sombra;

        &--boton{
            a{
               margin-top: 0; 
            }
            
        }
    }
    
    
}