@use "base/variables" as v;
@use "base/mixins" as m;

.header{

  background-size: cover;
  height: 40rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: v.$blanco;

  &__inicio {
    background-image: url(../img/hearder.jpg);
  }
  &__nosotros {
    background-image: url(../img/Fender.jpg);
  }
  &__contacto {
    background-image: url(../img/contacto.jpg);
  }

  &__barra {
    background: rgb(0, 0, 0);
    background: linear-gradient( 90deg, rgba(0, 0, 0, 0.8323704481792717) 50%, rgba(0, 0, 0, 0.7539390756302521) 51% );
    width: 100%;
    padding: 2rem;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    h1 {
        margin: 0;
        color: v.$rosa;
    }

  }

  &__navegacion {
    @include m.tablet {
        display: flex;
        gap: 2rem;
      }
      a {
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2rem;
        margin-bottom: 1rem;
        @include m.tablet {
          margin: 0;
        }
    
        &:hover {
          color: v.$amarillo;
        }
      }

  }


  &__texto {
    max-width: 50%;
    margin: 0 0 2rem 2rem;

    h1 {
        margin: 0;
        color: v.$blanco;
        text-transform: uppercase;
    }
    p {
        color: v.$blanco;
        font-size: 1.5rem;
    }

  }



}