@use "base/mixins" as m;
@use "base/variables" as v;

.nosotros {
  margin-top: 5rem;

  h2{
    color: v.$gris-claro;
    text-transform: uppercase;
    font-size: 3rem;
    margin-bottom: 2rem;
}

  &__grid {
    @include m.grid(3, 2rem);
  }

  &__card {
    @include m.sombra;
    text-align: center;
    padding: 1rem;

    i {
      color: v.$rosa;
      font-size: 6rem;
    }

    h4 {
      margin-bottom: 0;
      color: v.$gris-claro;
    }

    p {
      font-size: 1.5rem;
      margin-bottom: 0;
    }
  }
}
