@use 'base/mixins' as m;
@use 'base/variables' as v;

.mapa {
    margin-top: 5rem;
    text-align: center;
    

    h2{
        color: v.$gris-claro;
        text-transform: uppercase;
        font-size: 3rem;
        margin-bottom: 2rem;
    }

    &__card {
        @include m.sombra;
        margin: auto;
        padding: 1rem;
       
        iframe {
            width: 100%;
        }
    }
}