@use 'base/variables' as v;
@use 'base/mixins' as m;


.footer{
    background-color: v.$negro;
    margin-top: 5rem; 
    padding: 1rem 0;


    &__grid {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }

    &__contenido {
        h1 {
            color: v.$rosa;
            margin-bottom: 0;
        }
         p{
            color: v.$blanco;
            margin: 1rem 0 0 0;
         }
         &--bold {
            font-weight: v.$bold;
         }
    }
    


}

.site-footer {
    background-color: v.$rosa;
    padding: 1rem;
    p {
        margin: 0;
        text-align: center;
        color: v.$blanco;
    }
}


