@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario {

    &__card {
        @include m.sombra;
        padding: 2rem;
        width: 80%;
        margin: auto;
        margin-top: 3rem;

        p {
            font-size: 2rem;
            color: v.$gris-claro;
            font-weight: v.$bold;
        }
    }

    

    legend {
        font-size: 2rem;
        color: v.$grisOscuro;
    }

    label {
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }
}